*, ::before, ::after {
  box-sizing: inherit;
}

hr {
  border-top: 1px solid #554c88;
}

a {
  color: #7fff00;
}

.hover-animate {
  transition: transform ease-in-out .2s;
}

.hover-animate:hover {
  transform: scale(1.1);
}

select, input {
  padding: .3em;
  width: 100%;
}

#section .content {
  min-height: 90%;
  min-height: 90vh;
  display: flex;
  align-items: center;
}

.container {
  padding: 2px 16px;
}

.btn {
  background-color: #6a5acd;
  border: 1px solid transparent;
  color: white;
  font-weight: bold;
  border-radius: .475rem;
  padding: .4em;
  margin: 1.25em 0;
  cursor: pointer;
}

.btn:hover {
  background-color: #403b62;
}

.container-iframe {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
}

.responsive-iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

iframe {
  border: 0;
  border-radius: .2rem;
}

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  border-collapse: collapse;
}

.table td, .table th {
  padding: .5rem;
  vertical-align: top;
  border-top: 1px solid #6F6F6F;
}

.table .thead-light th {
  color: #495057;
  background-color: #e9ecef;
  border-color: #dee2e6;
}

.table-hover tbody tr:hover {
  background-color: rgba(0, 0, 0, .075);
}

.w3-purple {
  background-color: #403b62 !important;
}

.w3-container, .w3-panel {
  padding: 0;
}

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
#nav {
    background-color: #403b62;
    box-shadow: 0 .5em 1em rgba(0, 0, 0, .3);
}

#nav .logo {
    font-weight: bold;
    background-color: #28243e;
    height: 100%;
    width: 150px;
    line-height: 12px;
    font-size: .9em;
}

#nav .nav li {
    list-style: none;
}

#nav .container {
    padding: 0;
}

#nav .container ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.nav {
    margin: auto;
}

#nav ul li {
    padding: .5em;
}

#nav a {
    float: left;
    font-size: 1em;
    color: white;
    text-align: center;
    text-decoration: none;
    border-bottom: 2px solid transparent;
    border-top: 2px solid transparent;
    align-self: center;
    padding: .8em;
}

#nav .nav-link:hover {
    background-color: slateblue;
}

#nav .nav-link.active {
    background-color: slateblue;
    border-bottom: 2px solid white;
}

/* BOOTSTRAP TOGGLER*/

.fade {
    transition: opacity 0.15s linear;
}

@media (prefers-reduced-motion: reduce) {
    .fade {
        transition: none;
    }
}

.fade:not(.show) {
    opacity: 0;
}

.collapse:not(.show) {
    display: none;
}

.collapsing {
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
}

@media (prefers-reduced-motion: reduce) {
    .collapsing {
        transition: none;
    }
}

.collapsing.collapse-horizontal {
    width: 0;
    height: auto;
    transition: width 0.35s ease;
}

@media (prefers-reduced-motion: reduce) {
    .collapsing.collapse-horizontal {
        transition: none;
    }
}

@media (min-width: 992px) {
    .navbar-expand-lg {
        flex-wrap: nowrap;
        justify-content: flex-start;
    }
    .navbar-expand-lg .navbar-nav {
        flex-direction: row;
    }
    .navbar-expand-lg .navbar-nav .dropdown-menu {
        position: absolute;
    }
    .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 0.5rem;
        padding-left: 0.5rem;
    }
    .navbar-expand-lg .navbar-nav-scroll {
        overflow: visible;
    }
    .navbar-expand-lg .navbar-collapse {
        display: flex !important;
        flex-basis: auto;
    }
    .navbar-expand-lg .navbar-toggler {
        display: none;
    }
    .navbar-expand-lg .offcanvas-header {
        display: none;
    }
    .navbar-expand-lg .offcanvas {
        position: inherit;
        bottom: 0;
        z-index: 1000;
        flex-grow: 1;
        visibility: visible !important;
        background-color: transparent;
        border-right: 0;
        border-left: 0;
        transition: none;
        transform: none;
    }
    .navbar-expand-lg .offcanvas-top, .navbar-expand-lg .offcanvas-bottom {
        height: auto;
        border-top: 0;
        border-bottom: 0;
    }
    .navbar-expand-lg .offcanvas-body {
        display: flex;
        flex-grow: 0;
        padding: 0;
        overflow-y: visible;
    }
}

.navbar-toggler {
    padding: 0.25rem 0.75rem;
    font-size: 1.25rem;
    background-color: #6a5acd;
    border: 1px solid transparent;
    transition: box-shadow 0.15s ease-in-out;
    cursor: pointer;
    color: white;
}

@media (prefers-reduced-motion: reduce) {
    .navbar-toggler {
        transition: none;
    }
}

.navbar-toggler:hover {
    text-decoration: none;
}

.navbar-toggler:focus {
    text-decoration: none;
    outline: 0;
}

.navbar-toggler-icon {
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    vertical-align: middle;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}
